import { getFirestore } from "firebase/firestore"
import { doc, setDoc, collection } from "firebase/firestore"
import { initializeApp } from "firebase/app"
import {FIREBASE_CONFIG} from "../constants/utilsConstants"

class FirebaseManager {
    app = initializeApp(FIREBASE_CONFIG)
    firebaseDB = getFirestore(this.app)

    async createOrder(name, object) {
        const productsRef = doc(this.firebaseDB, 'Orders', name)
        await setDoc(productsRef, object)
    }
}

export default new FirebaseManager()