import {ROOT_FORM} from "../../constants/root"
import FirebaseManager from "../../utils/FirebaseManager"
import logo from './images/logo.svg'

import './Form.css'

class Form {
    render() {
        const html = `
            <div class="wrapper">
                <div class="service-container">
                    <div class="service__modal">
                        <div class="service__element">
                            <span class="service__name"></span>
                            <input class="service__item" type="radio" value="" name="service">
                        </div>
                        <div class="service__element">
                            <span class="service__name"></span>
                            <input class="service__item" type="radio" value="" name="service">
                        </div>
                    </div>
                </div>
                
                <div class="title">
                    <img src="${logo}" alt="">
                    <h1>Studio Control</h1>
                </div>
            
                <div class="form">
                    <div class="form__element">
                        <span class="form__span"></span>
                        <input type="text" class="form__input name" placeholder="  ">
                    </div>
                    <div class="form__element">
                        <span class="form__span"></span>
                        <input type="text" class="form__input nickname" placeholder="  ">
                    </div>
                    <div class="form__element">
                        <span class="form__span"></span>
                        <input type="text" class="form__input service" placeholder=" (/)">
                    </div>
                    <div class="form__element">
                        <span class="form__span"></span>
                        <input type="text" class="form__input contact" placeholder="  ">
                    </div>
                    <div class="form__element">
                        <span class="form__span"> </span>
                        <input type="text" class="form__input track-name" placeholder="  ">
                    </div>
                    <div class="form__element">
                        <span class="form__date"> </span>
                        <input type="text" class="form__input date" placeholder=" ">
                    </div>
                    <button class="form__button"></button>
                </div>
            </div>
        `

        ROOT_FORM.innerHTML = html

        this.eventListener()
    }

    eventListener() {
        const serviceButton = document.querySelector('.service')
        serviceButton.addEventListener('click', this.showModal)
        const button = document.querySelector('.form__button')
        button.addEventListener('click', this.createOrder)
    }

    createOrder() {
        let result = ''
        let characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'
        let charactersLength = characters.length
        for (let i = 0; i < length; i++) {
            result += characters.charAt(Math.floor(Math.random() * charactersLength))
        }

        FirebaseManager.createOrder('result', {
            contact: document.querySelector('.contact').value,
            date: document.querySelector('.date').value,
            isDone: false,
            name: document.querySelector('.name').value,
            nickname: document.querySelector('.nickname').value,
            price: document.querySelector('.service').value == '' ? 50 : 35,
            service: document.querySelector('.service').value,
            track: document.querySelector('.track-name').value
        })
    }

    showModal() {
        const modal = document.querySelector('.service-container')
        modal.style.display = 'flex'
        const services = document.querySelectorAll('.service__item')
        services.forEach((element) => {
            element.addEventListener('click', function() {
                const serviceInput = document.querySelector('.service')
                serviceInput.value = this.value
                modal.style.display = 'none'
            })
        })
    }
}

export default new Form()